html {
  --text-color: #000;
  --text-hover-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body, div, p, h1, h2, h3, a, li, ul, nav {
  margin: 0;
  padding: 0;
}

.container {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  padding: 10px 0;
  display: flex;
}

.nav {
  display: flex;
}

.nav-order {
  list-style: none;
  display: flex;
}

.nav-link {
  color: var(--text-color);
  text-align: left;
  text-transform: uppercase;
  padding-top: 22px;
  padding-bottom: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
}

.nav-link:hover {
  color: var(--text-hover-color);
}

.nav-element:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--text-hover-color);
  border-radius: 10px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

